}

.wizard-container .form-group input:hover {
  border-bottom-color: #1e22aa;
  box-shadow: none;
  transform: none;
}

.wizard-container .form-group input:focus {
  outline: none;
  border-bottom-color: #1e22aa;
  border-bottom-width: 3px;
  background: #ffffff;
  box-shadow: none;
  transform: none;
}

.wizard-container .form-group input::placeholder {
  color: #9ca3af;
  font-weight: 400;
  opacity: 0.8;
}

.wizard-container .form-group select {
  width: 100%;
  padding: 1rem 1.25rem;
  border: none;
  border-bottom: 2px solid #e2e8f0;
  border-radius: 0;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  background: #ffffff;
  color: #1f2937;
  box-shadow: none;
  min-height: 56px;
  font-family: 'Boston', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, sans-serif;
  cursor: pointer;
}

.wizard-container .form-group select:hover {
  border-bottom-color: #1e22aa;
  box-shadow: none;
  transform: none;
}

.wizard-container .form-group select:focus {
  outline: none;
  border-bottom-color: #1e22aa;
  border-bottom-width: 3px;
  background: #ffffff;
  box-shadow: none;
  transform: none;
}

.wizard-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  padding-top: 2rem;
