}

.wizard-actions .btn-primary {
  background: #1e22aa;
  color: white;
  border-color: #1e22aa;
}

.wizard-actions .btn-primary:hover {
  background: #151a8a;
  border-color: #151a8a;
  transform: translateY(-3px);
  box-shadow: 0 8px 32px rgba(30, 34, 170, 0.3);
}

.wizard-actions .btn-secondary {
  background: #ffffff;
  color: #1e22aa;
  border-color: #1e22aa;
}

.wizard-actions .btn-secondary:hover {
  background: #1e22aa;
  color: white;
  transform: translateY(-3px);
