  margin-top: 0.25rem;
  font-size: 0.8rem;
  color: #666;
  font-style: italic;
}

.premios-section, .reglas-section, .fotos-section {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 2px solid #e1e5e9;
}

.premios-section h3, .reglas-section h3, .fotos-section h3 {
  margin: 0 0 1rem 0;
  color: #333;
  font-size: 1.2rem;
}

.premio-item {
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
  background: #f8f9fa;
  position: relative;
}

.btn-eliminar {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background: #f44336;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.25rem 0.5rem;
  font-size: 0.8rem;
  cursor: pointer;
}

.btn-eliminar:hover {
  background: #d32f2f;
}

.form-group textarea {
  width: 100%;
  padding: 0.75rem 0;
  border: none;
  border-bottom: 2px solid #e5e7eb;
  border-radius: 0;
  font-size: 1rem;
  font-family: 'Boston', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, sans-serif;
  resize: vertical;
  transition: all 0.2s ease;
  background: transparent;
  color: #1f2937;
  min-height: 80px;
  line-height: 1.5;
}

.form-group textarea:hover {
  border-bottom-color: #d1d5db;
}

.form-group textarea:focus {
  outline: none;
  border-bottom-color: #1e22aa;
  border-bottom-width: 2px;
}

.file-input {
  display: none;
}

.file-label {
  display: inline-block;
  background: #f8f9fa;
  border: 2px dashed #e1e5e9;
  border-radius: 8px;
  padding: 2rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
}

.file-label:hover {
  border-color: #d4af37;
  background: #f0f4ff;
}

.fotos-preview {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.foto-item {
  position: relative;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  overflow: hidden;
}

.foto-item img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.btn-eliminar-foto {
  position: absolute;
  top: 0.25rem;
  right: 0.25rem;
  background: rgba(244, 67, 54, 0.8);
  color: white;
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  font-size: 0.8rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-eliminar-foto:hover {
  background: #f44336;
}

/* Estilos para vista pblica - Premios */
.premios-public, .fotos-premios-public, .reglas-public {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.premios-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.premio-public {
  border: 2px solid #e1e5e9;
  border-radius: 12px;
  padding: 1.5rem;
  text-align: center;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  transition: transform 0.2s ease;
}

.premio-public:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.premio-posicion {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.premio-public h4 {
  margin: 0 0 0.5rem 0;
  color: #333;
  font-size: 1.1rem;
}

.premio-public p {
  margin: 0;
  color: #666;
  font-size: 0.9rem;
}

/* Estilos para fotos en vista pblica */
.fotos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.foto-premio {
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.2s ease;
}

.foto-premio:hover {
  transform: scale(1.02);
}

.foto-premio img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

/* Estilos para reglas */
.reglas-content {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 1.5rem;
  border-left: 4px solid #d4af37;
}

.reglas-content p {
  margin: 0;
  line-height: 1.6;
  color: #333;
  white-space: pre-wrap;
}

/* Estilos para gestin - Premios */
.premios-management, .fotos-management, .reglas-management {
  background: white;
  border-radius: 15px;
  padding: 1.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.premios-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.premio-management {
  display: flex;
  align-items: center;
  gap: 1rem;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  padding: 1rem;
  background: #f8f9fa;
}

.premio-management .premio-posicion {
  font-size: 1.5rem;
  min-width: 40px;
  text-align: center;
}

.premio-info h4 {
  margin: 0 0 0.25rem 0;
  color: #333;
}

.premio-info p {
  margin: 0;
  color: #666;
  font-size: 0.9rem;
}

/* Estilos para fotos en gestin */
.fotos-management-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 1rem;
}

.foto-management {
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  overflow: hidden;
}

.foto-management img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

/* Responsive para nuevas secciones */
@media (max-width: 768px) {
  .premios-grid {
    grid-template-columns: 1fr;
  }

  .fotos-grid {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  }

  .fotos-preview {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  }

  .fotos-management-grid {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  }

  .premio-management {
    flex-direction: column;
    text-align: center;
  }

  .premio-management .premio-posicion {
    min-width: auto;
  }
}

/* Estilos para formas de pago */
.pagos-section {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 2px solid #e1e5e9;
}

.pagos-section h3 {
  margin: 0 0 1rem 0;
  color: #333;
  font-size: 1.2rem;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  font-weight: 600;
  color: #333;
}

.checkbox-label input[type="checkbox"] {
  width: auto;
  margin: 0;
}

.pagos-details {
  margin-top: 1rem;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
  border: 2px solid #e1e5e9;
}

/* Estilos para portal de rifas */
.portal-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: 100vh;
  background: #f8f9fa;
}

.portal-header {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  text-align: center;
  color: #1e22aa;
  margin-bottom: 2rem;
  border: 1px solid rgba(30, 34, 170, 0.1);
  box-shadow: 0 8px 32px rgba(30, 34, 170, 0.1);
}

.portal-header h1 {
  margin: 0 0 1rem 0;
  font-size: 2.5rem;
  font-weight: 700;
  color: #1e22aa;
}

.portal-header p {
  color: #666;
  font-size: 1.1rem;
  margin: 0;
}

.portal-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  margin-top: 1.5rem;
}

.stat-item {
  border-radius: 10px;
  padding: 1rem;
  text-align: center;
  border: 1px solid rgba(30, 34, 170, 0.2);
  box-shadow: 0 4px 16px rgba(30, 34, 170, 0.2);
}

.stat-number {
  display: block;
  font-size: 2rem;
  font-weight: 700;
  color: white;
  margin-bottom: 0.25rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

.stat-label {
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.9rem;
  font-weight: 500;
}

.portal-controls {
  background: white;
  border-radius: 15px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: 0 10px 30px rgba(30, 34, 170, 0.1);
  border: 1px solid rgba(30, 34, 170, 0.05);
}

.search-box {
  margin-bottom: 1rem;
}

.search-input {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  background: #f8f9fa;
}

.search-input:focus {
  outline: none;
  border-color: #1e22aa;
  background: white;
  box-shadow: 0 0 0 3px rgba(30, 34, 170, 0.1);
}

.filtros-portal {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.filtro-portal-btn {
  background: #f8f9fa;
  color: #333;
  border: 2px solid #e1e5e9;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

.filtro-portal-btn:hover {
  background: #e9ecef;
  border-color: #1e22aa;
  color: #1e22aa;
}

.filtro-portal-btn.activo {
  background: #1e22aa;
  color: white;
  border-color: #1e22aa;
  box-shadow: 0 2px 8px rgba(30, 34, 170, 0.3);
}

.rifas-portal-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
}

.rifa-portal-card {
  background: white;
  border-radius: 15px;
  padding: 1.5rem;
  box-shadow: 0 10px 30px rgba(30, 34, 170, 0.1);
  transition: all 0.3s ease;
  border: 1px solid rgba(30, 34, 170, 0.05);
}

.rifa-portal-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 15px 40px rgba(30, 34, 170, 0.15);
  border-color: rgba(30, 34, 170, 0.1);
}

.rifa-portal-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.rifa-portal-header h3 {
  margin: 0;
  color: #1e22aa;
  font-size: 1.2rem;
  font-weight: 600;
}

.rifa-status {
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  font-size: 0.8rem;
  font-weight: 600;
}

.rifa-status.activa {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
  box-shadow: 0 2px 8px rgba(16, 185, 129, 0.3);
}

.rifa-status.finalizada {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
  box-shadow: 0 2px 8px rgba(239, 68, 68, 0.3);
}

.rifa-descripcion {
  color: #666;
  margin-bottom: 1rem;
  font-size: 0.9rem;
  line-height: 1.4;
}

.rifa-portal-info {
  margin-bottom: 1rem;
}

.info-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  color: #555;
}

.rifa-premios-preview {
  margin-bottom: 1rem;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
}

.rifa-premios-preview h4 {
  margin: 0 0 0.5rem 0;
  color: #333;
  font-size: 1rem;
}

.premios-preview-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.premio-preview {
  background: white;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  border: 1px solid #e1e5e9;
}

.rifa-portal-actions {
  display: flex;
  gap: 0.5rem;
}

.btn-portal-primary, .btn-portal-secondary {
  flex: 1;
  padding: 0.5rem;
  border-radius: 6px;
  text-decoration: none;
  text-align: center;
  font-weight: 600;
  transition: all 0.2s ease;
}

.btn-portal-primary {
  background: linear-gradient(135deg, #1e22aa 0%, #2c3eaa 100%);
  color: white;
  border: 2px solid #1e22aa;
  box-shadow: 0 2px 8px rgba(30, 34, 170, 0.3);
}

.btn-portal-primary:hover {
  background: linear-gradient(135deg, #2c3eaa 0%, #1e22aa 100%);
  box-shadow: 0 4px 12px rgba(30, 34, 170, 0.4);
  transform: translateY(-1px);
}

.btn-portal-secondary {
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  color: #1e22aa;
  border: 2px solid #1e22aa;
  font-weight: 600;
}

.btn-portal-secondary:hover {
  background: linear-gradient(135deg, #1e22aa 0%, #2c3eaa 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(30, 34, 170, 0.3);
  transform: translateY(-1px);
}

.no-rifas-portal {
  background: white;
  border-radius: 15px;
  padding: 3rem;
  text-align: center;
  box-shadow: 0 10px 30px rgba(30, 34, 170, 0.1);
  border: 1px solid rgba(30, 34, 170, 0.05);
  grid-column: 1 / -1;
}

.no-rifas-portal h3 {
  color: #1e22aa;
  margin-bottom: 1rem;
}

/* Botn de regreso a rifas */
.header-top {
  margin-bottom: 1rem;
}

.btn-back-to-rifas {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: linear-gradient(135deg, #1e22aa 0%, #2c3eaa 100%);
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(30, 34, 170, 0.2);
  border: 1px solid rgba(30, 34, 170, 0.1);
}

.btn-back-to-rifas:hover {
  background: linear-gradient(135deg, #2c3eaa 0%, #1e22aa 100%);
  box-shadow: 0 4px 12px rgba(30, 34, 170, 0.3);
  transform: translateY(-1px);
  color: white;
  text-decoration: none;
}

/* Estilos para das restantes en cards */
.rifa-portal-info .info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.rifa-portal-info .info-row span {
  font-size: 0.9rem;
  color: #666;
}

/* Destacar das restantes cuando quedan pocos */
.rifa-portal-info .info-row span:first-child {
  font-weight: 600;
}

