.rifa-portal-card[data-urgency="high"] .rifa-portal-info .info-row span:first-child {
  color: #ef4444;
  font-weight: 700;
}

.rifa-portal-card[data-urgency="medium"] .rifa-portal-info .info-row span:first-child {
  color: #f59e0b;
  font-weight: 600;
