.hero-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.btn-hero-primary, .btn-hero-secondary {
  padding: 1rem 2rem;
  border-radius: 16px;
  font-size: 1.1rem;
  font-weight: 600;
  font-family: 'Boston', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, sans-serif;
