/* Checkbox personalizado para registro opcional */
.checkbox-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 1rem;
  color: #1e22aa;
  font-weight: 500;
  margin: 10px 0;
  padding: 10px;
  background: rgba(30, 34, 170, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(30, 34, 170, 0.1);
  transition: all 0.3s ease;
}

.checkbox-label:hover {
  background: rgba(30, 34, 170, 0.1);
  border-color: rgba(30, 34, 170, 0.2);
}

.checkbox-label input[type="checkbox"] {
  display: none;
}

.checkmark {
  width: 20px;
  height: 20px;
  border: 2px solid #1e22aa;
  border-radius: 4px;
  margin-right: 12px;
  position: relative;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.checkbox-label input[type="checkbox"]:checked + .checkmark {
  background: #1e22aa;
  border-color: #1e22aa;
}

.checkbox-label input[type="checkbox"]:checked + .checkmark::after {
  content: '';
  position: absolute;
  left: 6px;
  top: 2px;
  width: 6px;
  height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.info-message {
  background: #e3f2fd;
  border: 1px solid #90caf9;
  border-radius: 8px;
  padding: 12px;
  margin: 10px 0;
}

