  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
}

.user-info span {
  color: white;
  font-weight: 500;
  font-size: 1rem;
}

.btn-logout {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 0.5rem 1rem;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  backdrop-filter: blur(10px);
}

.btn-logout:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-1px);
}

/* Login Section Styles */
.login-section {
  margin-top: 2rem;
  padding: 1.5rem;
  border-radius: 16px;
  backdrop-filter: blur(10px);
  text-align: center;
}

.login-section p {
  color: white;
  font-weight: 600;
  margin: 0 0 1rem 0;
}

.btn-hero-login {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  backdrop-filter: blur(10px);
}

.btn-hero-login:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-2px);
}

/* Search Section Styles */
.search-section {
  margin-top: 1.5rem;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  text-align: center;
}

.search-section p {
  color: white;
  font-weight: 600;
  margin: 0 0 1rem 0;
}

.search-input {
  display: flex;
  gap: 0.5rem;
  max-width: 400px;
  margin: 0 auto;
}

.search-input input {
  flex: 1;
  padding: 0.75rem 1rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 0.9rem;
  backdrop-filter: blur(10px);
}

.search-input input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.search-input input:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.5);
  background: rgba(255, 255, 255, 0.15);
}

.btn-search {
  background: linear-gradient(135deg, #1e22aa 0%, #3b82f6 100%);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  white-space: nowrap;
}

.btn-search:hover {
  background: linear-gradient(135deg, #3b82f6 0%, #1e22aa 100%);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(30, 34, 170, 0.3);
}

/* Guest Welcome Styles */
.welcome-guest {
  text-align: center;
  padding: 4rem 2rem;
  max-width: 600px;
  margin: 0 auto;
}

.welcome-guest h2 {
  font-size: 2.5rem;
  font-weight: 700;
  font-family: 'Boston', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, sans-serif;
  color: #1e293b;
  margin: 0 0 1.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.welcome-guest p {
  font-size: 1.2rem;
  color: #64748b;
  line-height: 1.6;
  margin: 0 0 2rem 0;
}

.guest-actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

/* Visibility Options Styles */
.visibility-options {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 0.5rem;
}

.visibility-options-compact {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
  margin-top: 0.5rem;
}

.radio-option {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 1rem;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  transition: all 0.3s ease;
  background: #ffffff;
}

.radio-option-compact {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0.75rem 1rem;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  transition: all 0.2s ease;
  background: #ffffff;
  flex: 1;
  gap: 0.75rem;
}

.radio-option:hover {
  border-color: #1e22aa;
  background: #f8fafc;
}

.radio-option-compact:hover {
  border-color: #1e22aa;
  background: #f8fafc;
}

.radio-option input[type="radio"] {
  margin-right: 1rem;
  width: 18px;
  height: 18px;
  accent-color: #1e22aa;
}

.radio-option-compact input[type="radio"] {
  margin-right: 0.5rem;
  width: 16px;
  height: 16px;
  accent-color: #1e22aa;
}

.radio-option input[type="radio"]:checked + .radio-label {
  color: #1e22aa;
  font-weight: 600;
}

.radio-option-compact input[type="radio"]:checked + .radio-label-compact {
  color: #1e22aa;
  font-weight: 600;
}

.radio-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #64748b;
  font-weight: 500;
  transition: all 0.3s ease;
}

.radio-label-compact {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  color: #64748b;
  font-weight: 500;
  transition: all 0.2s ease;
  font-size: 0.9rem;
}

/* Checkbox styles */
.form-group input[type="checkbox"] {
  margin-right: 0.5rem;
  width: 18px;
  height: 18px;
  accent-color: #1e22aa;
  cursor: pointer;
}

.form-group label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 1rem;
  color: #374151;
  font-weight: 500;
}

.radio-icon {
  font-size: 1.2rem;
}

/* Success Modal Styles */
.success-modal {
  text-align: center;
  max-width: 500px;
  padding: 2rem;
}

.success-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  animation: bounce 1s ease-in-out;
}

.success-modal h2 {
  color: #1f2937;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.success-modal p {
  color: #64748b;
  font-size: 1.1rem;
  margin-bottom: 2rem;
}

.success-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.success-actions .btn-primary,
.success-actions .btn-secondary {
  min-width: 150px;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

.radio-option:has(input:checked) {
  border-color: #1e22aa;
  background: rgba(30, 34, 170, 0.05);
}

.radio-option:has(input:checked) .radio-label {
  color: #1e22aa;
  font-weight: 600;
}

/* Estilos para Modal de Venta */
.venta-modal {
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
}

.tipo-venta-options {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 0.5rem;
}

.tipo-venta-options .radio-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem;
  background: #f8fafc;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tipo-venta-options .radio-option:hover {
  border-color: #1e22aa;
  background: #f1f5f9;
}

.tipo-venta-options .radio-option input[type="radio"] {
