  font-weight: 600;
}

.participante-form {
  display: flex;
  gap: 1rem;
}

.participante-form .form-group {
  flex: 1;
  margin-bottom: 0;
}

.participante-form .form-group label {
  display: block;
  margin-bottom: 0.5rem;
