/* Estilos para la seccin de venta */
.venta-form {
  margin-bottom: 1.5rem;
}

.venta-form .form-group {
  margin-bottom: 1.5rem;
}

.venta-form .form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #6b7280;
