  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
}

.resumen-venta h3 {
  color: #1e293b;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.resumen-stats {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.resumen-stats .stat {
  display: flex;
  justify-content: space-between;
