}

.resumen-stats .stat.total {
  background: #1e22aa;
  color: white;
  font-weight: 600;
  font-size: 1.1rem;
}

/* Estilos para header actions */
.header-actions {
  display: flex;
  gap: 0.75rem;
  align-items: center;
}

.header-actions .btn-secondary {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
