  font-weight: 600;
  border-radius: 10px;
  min-height: 48px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  text-decoration: none;
  transition: all 0.3s ease;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}

.dashboard-footer .btn-primary {
  background: #1e22aa;
  color: white;
  border: none;
  box-shadow: 0 3px 10px rgba(30, 34, 170, 0.25);
}

.dashboard-footer .btn-primary:hover {
  background: #151a8a;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(30, 34, 170, 0.35);
}

.dashboard-footer .btn-secondary {
  background: white;
  color: #1e22aa;
  border: 2px solid #1e22aa;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
}

.dashboard-footer .btn-secondary:hover {
  background: #1e22aa;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(30, 34, 170, 0.25);
}

/* Estilos especficos para tarjetas del dashboard */
.dashboard .participacion-card {
  margin-bottom: 0.75rem;
}

.dashboard .participacion-header h3 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.dashboard .participacion-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 0.5rem;
  margin-bottom: 0.75rem;
}

.dashboard .participacion-detail {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  font-size: 0.8rem;
  color: #64748b;
}

.dashboard .participacion-detail i {
  font-size: 0.7rem;
  color: #1e22aa;
}

.dashboard .participacion-stats {
  display: flex;
  gap: 1rem;
  margin-bottom: 0.75rem;
  font-size: 0.8rem;
  color: #64748b;
}

.dashboard .participacion-stats span {
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

/* Estilos para tabla de rifas */
.table-container {
  background: white;
  border-radius: 10px;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  border: 1px solid #e2e8f0;
  overflow-x: auto;
}

.rifas-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.9rem;
}

.rifas-table thead {
  background: #f8fafc;
  border-bottom: 2px solid #e2e8f0;
}

.rifas-table th {
  padding: 1rem 0.75rem;
  text-align: left;
  font-weight: 600;
  color: #1e22aa;
  font-size: 0.85rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.rifas-table td {
  padding: 1rem 0.75rem;
  border-bottom: 1px solid #f1f5f9;
  vertical-align: middle;
}

.rifas-table tbody tr:hover {
  background: #f8fafc;
}

.rifa-name {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.rifa-name strong {
  color: #1e22aa;
  font-size: 0.95rem;
  font-weight: 600;
}

.rifa-description {
  color: #64748b;
  font-size: 0.8rem;
  font-style: italic;
}

.rifa-type {
  background: #e0f2fe;
  color: #0277bd;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 500;
}

.rifa-price {
  color: #059669;
  font-weight: 600;
  font-size: 0.9rem;
}

.progress-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  min-width: 80px;
}

.progress-text {
  font-size: 0.8rem;
  color: #64748b;
  font-weight: 500;
}

.progress-bar {
  width: 100%;
  height: 6px;
  background: #e2e8f0;
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #1e22aa, #3b82f6);
  border-radius: 3px;
  transition: width 0.3s ease;
}

.participants-count {
  color: #1e22aa;
  font-weight: 600;
  font-size: 0.9rem;
}

.status-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 500;
}

.status-badge.active {
  background: #dcfce7;
  color: #166534;
}

.status-badge.finished {
  background: #fef2f2;
  color: #dc2626;
}

.end-date {
  color: #64748b;
  font-size: 0.85rem;
}

.action-buttons {
  display: flex;
  gap: 0.5rem;
}

.btn-manage {
  background: linear-gradient(135deg, #1e22aa, #3b82f6);
  color: white;
  border: none;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  font-size: 0.8rem;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-manage:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 6px rgba(30, 34, 170, 0.2);
}

.btn-view {
  background: white;
  color: #64748b;
  border: 1px solid #e2e8f0;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  font-size: 0.8rem;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-view:hover {
  background: #f8fafc;
  border-color: #1e22aa;
  color: #1e22aa;
}

/* ========================================
   SISTEMA RESPONSIVE: DESKTOP vs MVIL
   ======================================== */

/* Por defecto: mostrar tabla en desktop */
.table-container.desktop-only {
  display: block !important;
}

.rifas-cards.mobile-only {
  display: none !important;
}

/* En mviles (< 768px): cambiar a cards */
@media (max-width: 768px) {
  .table-container.desktop-only {
    display: none !important;
  }

  .rifas-cards.mobile-only {
    display: flex !important;
  }
}

/* ========================================
   AURELA CARDS PREMIUM - DISEO SORPRENDENTE
   ======================================== */

/* Contenedor de cards */
.rifas-cards {
  display: flex !important;
  flex-direction: column !important;
  gap: 1.5rem !important;
  padding: 1rem !important;
  background: #f8fafc !important;
