/* Scroll Container para Mvil */
.rifas-scroll-container.mobile-only {
  display: none !important;
}

/* En mviles (< 768px): mostrar scroll container */
@media (max-width: 768px) {
  .rifas-scroll-container.mobile-only {
    display: block !important;
    margin: 1.5rem 0;
  }
}

.scroll-wrapper {
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.scroll-indicators {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  background: linear-gradient(135deg, #1e22aa, #3b82f6);
  color: white;
  gap: 0.75rem;
}

.scroll-counter {
  font-weight: 700;
  font-size: 1rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.scroll-dots {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 200px;
}

.scroll-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 255, 255, 0.4);
  cursor: pointer;
  transition: all 0.3s ease;
}

.scroll-dot.active {
  background: white;
  transform: scale(1.2);
}

.scroll-dot:hover {
  background: rgba(255, 255, 255, 0.7);
}

.scroll-container {
  height: 700px; /* Altura aumentada para mostrar completamente los botones */
  overflow-y: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
}

.scroll-card {
  scroll-snap-align: start;
  margin-bottom: 1rem;
  transition: all 0.3s ease;
}

.scroll-card.active {
  transform: scale(1.02);
  box-shadow: 0 8px 25px rgba(30, 34, 170, 0.15);
}

/* Personalizar scrollbar */
.scroll-container::-webkit-scrollbar {
  width: 6px;
}

.scroll-container::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 3px;
}

.scroll-container::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #1e22aa, #3b82f6);
  border-radius: 3px;
}

.scroll-container::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #0f1280, #2563eb);
}

/* Responsive para scroll container */
@media (max-width: 480px) {
  .scroll-container {
    height: 600px; /* Altura ajustada para mviles pequeos */
  }
  
  .scroll-indicators {
    padding: 0.75rem;
    gap: 0.5rem;
  }
  
  .scroll-counter {
    font-size: 0.9rem;
  }
  
  .scroll-dots {
    max-width: 150px;
  }
  
  .scroll-dot {
    width: 6px;
    height: 6px;
  }
}

